@import "./variables";
.navigationWidthContainer {
  width: 100vw;
  height: 10vh;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.5rem;
  background-image: $primaryColor;

  .darkModeContainer {
    width: 30vw;
    margin-left: 5vw;
  }
  .logeContainerDay {
    line-height: 10vh;
    height: 10vh;
    width: 30vw;
    text-align: center;
    .logoLinkDay {
      display: block;
      height: 10vh;
      cursor: pointer;
      text-decoration: none;
      font-size: 2rem;
      color: $sixthColor;
    }
  }
  .hamburgerMenuDay {
    display: none;
    cursor: pointer;
    @media screen and (max-width: 1028px) {
      display: block;
      width: 30vw;
      text-align: right;
      margin-right: 5vw;
      transition: all 0.35s ease-in-out;
    }
    .fa-times {
      color: $sixthColor;
    }
    .fa-bars {
      color: $sixthColor;
    }
  }
  .menuActive {
    width: 30vw;
    margin-right: 5vw;
    display: grid;
    grid-template-columns: (repeat(4, auto));
    grid-gap: 10px;
    text-align: center;
    transition: all 0.35s ease;
    @media screen and (max-width: 1030px) {
      margin-right: 0vw;
      position: absolute;
      display: flex;
      flex-direction: column;
      grid-gap: 0px;
      top: 10vh;
      height: 80vh;
      width: 100%;
      animation: activeMenuAnimation 0.4s linear;
      left: 0%;
      text-align: center;
      z-index: 10;
      @keyframes activeMenuAnimation {
        0% {
          left: -100%;
        }
        50% {
          left: -50%;
        }
        100% {
          left: 0%;
        }
      }
    }
    .liElementDay {
      display: flex;
      height: 10vh;
      line-height: 10vh;
      width: 100%;
      @media screen and (max-width: 1030px) {
        display: flex;
        height: 20vh;
        line-height: 20vh;
        justify-content: center;
      }
      .liLinkElement {
        height: 10vh;
        text-decoration: none;
        @media screen and (max-width: 1030px) {
          height: 20vh;
          line-height: 20vh;
          width: 100%;
        }
      }
    }
  }

  .menuUnactive {
    width: 30vw;
    margin-right: 5vw;
    display: grid;
    grid-template-columns: (repeat(4, auto));
    grid-gap: 10px;
    text-align: center;
    transition: all 0.35 ease;

    @media screen and (max-width: 1030px) {
      margin-right: 0vw;
      position: absolute;
      display: flex;
      flex-direction: column;
      grid-gap: 0px;
      top: 10vh;

      height: 80vh;
      width: 100%;
      animation: unactiveMenuAnimation 0.4s linear;
      left: -100%;
      z-index: 10;

      @keyframes unactiveMenuAnimation {
        0% {
          left: 0%;
        }
        50% {
          left: -50%;
        }
        100% {
          left: -100%;
        }
      }
    }
    .liElementDay {
      display: flex;
      height: 10vh;
      line-height: 10vh;
      width: 100%;
      @media screen and (max-width: 1030px) {
        display: flex;
        height: 20vh;
        line-height: 20vh;
        justify-content: center;
      }
      .liLinkElement {
        height: 10vh;
        text-decoration: none;
        @media screen and (max-width: 1030px) {
          height: 20vh;
          line-height: 20vh;
          width: 100%;
        }
      }
    }
  }
}
.hamburgerMenuDay {
  color: $sixthColor;
  transition: all 0.35s ease-in-out;
  &:hover {
    color: $thirdColor;
  }
}
.logoLinkDay {
  color: $sixthColor;
  transition: all 0.35s ease-in;
  @media  screen and (min-width: 1030px) {
    &:hover {
      color: $thirdColor !important;
    }
  }
 
}
.menuDayMode {
  @media screen and (max-width: 1030px) {
    background-image: $invertedPrimaryColor;
  }
}
.liLinkElement {
  color: $sixthColor;
  transition: all 0.35s ease-in;

  @media screen and (max-width: 1030px) {
    &:hover {
      border-bottom: none;
    }
  }
  @media  screen and (min-width: 1030px) {
    &:hover {
      color: $thirdColor;
    }
    
  }
}

.darkMode {
  .navigationWidthContainer {
    background-image: $secondaryColor;
    .logeContainerDay {
      .logoLinkDay {
        color: $fifthColor;
        @media screen and (min-width: 1030px) {
          &:hover {
            color: $fourthColor !important;
          }
        }
      }
    }
    .hamburgerMenuDay {
      .fa-bars {
        color: $fifthColor;
      }
      .fa-times {
        color: $fifthColor;
      }
    }
    .menuDayMode {
      background-image: $invertedSecondaryColor;
      @media  screen and (min-width: 1030px) {
        background-image: none;
      }
      .liElementDay {
        .liLinkElement {
          color: $fifthColor;
          @media  screen and (min-width: 1030px) {
            &:hover {
              color: $fourthColor;
            }
          }
        
        }
      }
    }
  }
}
